
import Laptop from "@/MyTypes/laptob";
import axios from "axios";

interface IProductProps {
  params: Promise<{id: string}>;
  searchParams: Promise<object>;
}

async function Product(props: IProductProps) {
  const { id } = await props.params;

  console.log(id);

  const response = await axios.get(`http://localhost:5000/api/laptops/${id}`);
  const laptop: Laptop = response.data;

  return (

    <div className="flex flex-col items-center p-5">
      <div className="flex justify-center mb-5">
        <img src={laptop.image} alt={`Product ${id}`} className="max-w-full h-auto" />
      </div>
      <div className="text-center">
        <h1 className="text-3xl font-bold mb-2">{laptop.name}</h1>
        <p className="text-2xl text-red-500 mb-4">{laptop.price} تومان</p>
        <p className="text-lg mb-4">{laptop.description}</p>
        <ul className="text-lg list-none p-0">
          <li className="mb-2"><strong>Brand:</strong> {laptop.brand}</li>
          <li className="mb-2"><strong>Model:</strong> {laptop.model}</li>
          {laptop.processor && <li className="mb-2"><strong>Processor:</strong> {laptop.processor}</li>}
          {laptop.usageType && <li className="mb-2"><strong>Usage Type:</strong> {laptop.usageType}</li>}
        </ul>
      </div>
    </div>
  
  );
}

export default Product;
